(function() {
  this.TheCreem = angular.module('creemAngular', ['Devise', 'ui.router', 'ngMaterial', 'ngMessages', 'toastr', 'uiGmapgoogle-maps']);

}).call(this);



(function() {
  this.TheCreem.config(function(AuthProvider, AuthInterceptProvider) {
    AuthProvider.loginMethod('POST');
    AuthProvider.loginPath('/auth/users/sign_in.json');
    AuthProvider.logoutMethod('DELETE');
    AuthProvider.logoutPath('/auth/users/sign_out.json');
    AuthProvider.registerMethod('POST');
    return AuthProvider.registerPath('/auth/users');
  });

  this.TheCreem.config(function($mdThemingProvider) {
    var creemOrangeMap;
    creemOrangeMap = $mdThemingProvider.extendPalette('deep-orange', {
      '500': 'f26c4f'
    });
    $mdThemingProvider.definePalette('creemOrange', creemOrangeMap);
    $mdThemingProvider.theme('default').primaryPalette('creemOrange').accentPalette('grey', {
      'default': '900',
      'hue-1': '200',
      'hue-2': '700',
      'hue-3': '800'
    });
    return $mdThemingProvider.alwaysWatchTheme(true);
  });

}).call(this);



(function() {
  this.TheCreem.constant('USER_ROLES', {});

}).call(this);



(function() {
  this.TheCreem.config(function($stateProvider, $urlRouterProvider) {
    $urlRouterProvider.otherwise('/');
    return $stateProvider.state('landing', {
      url: '/',
      templateUrl: 'app/views/landing/landing.layout.html',
      controller: 'LandingController',
      controllerAs: 'vm'
    }).state('platform', {
      abstract: true,
      templateUrl: 'app/views/platform/platform.layout.html',
      controller: 'PlatformController',
      controllerAs: 'main'
    }).state('platform.profile', {
      url: '/profile',
      templateUrl: 'app/views/platform/profile/profile.layout.html',
      controller: 'ProfileController',
      controllerAs: 'vm',
      data: {
        heading: 'My Profile'
      }
    });
  });

}).call(this);



(function() {
  this.TheCreem.run(function($log, $rootScope, $state, Auth, $http, $timeout) {
    return $rootScope.$on('$stateChangeStart', function(event, next) {
      return Auth.currentUser().then(function(user) {
        return $log.debug('Authorized user session');
      }, function(error) {
        return $log.debug('Unauthorized, sign in or sign up to continue');
      });
    });
  });

}).call(this);



(function() {
  this.TheCreem.directive('creemTopnav', function() {
    var directive;
    return directive = {
      restrict: 'E',
      templateUrl: 'app/components/creem-topnav/creem-topnav.html',
      scope: {},
      controllerAs: 'vm',
      bindToController: true,
      controller: function() {
        var vm;
        vm = this;
      }
    };
  });

}).call(this);



(function() {
  this.TheCreem.controller('LandingController', function($log, $anchorScroll, $location, $window, Auth, CreemSession) {
    var vm;
    vm = this;
    vm.map = {
      zoom: 12,
      center: {
        longitude: 151.2094,
        latitude: -33.8650
      }
    };
    vm.mapOptions = {
      scrollwheel: false
    };
    vm.showRegisterForm = function() {
      return vm.showLoginForm = 'register';
    };
    vm.afterLoginCb = function(user) {
      return CreemSession.create(user);
    };
    vm.afterRegisterCb = function(user) {
      return CreemSession.create(user);
    };
    vm.afterResetCb = function() {};
    vm.toggleMobileMenu = function() {
      var el;
      el = angular.element(document.getElementById('mobile-nav-bar'));
      return el.toggleClass('open');
    };
    vm.goToAnchor = function(id, mobileMenu) {
      $location.hash(id);
      $anchorScroll();
      if (mobileMenu) {
        return vm.toggleMobileMenu();
      }
    };
    vm.goToRegister = function(mobileMenu) {
      window.location.hash = '';
      vm.showRegisterForm();
      $window.scrollTo(0, 0);
      if (mobileMenu) {
        return vm.toggleMobileMenu();
      }
    };
    return this;
  });

}).call(this);



(function() {
  this.TheCreem.controller('PlatformController', function($log, Auth) {
    var main;
    main = this;
    main.foo = "foobar";
    return null;
  });

}).call(this);



(function() {
  this.TheCreem.directive('creemLogin', function($log, Auth) {
    var directive;
    return directive = {
      restrict: 'EA',
      templateUrl: 'app/components/creem-login/creem-login.html',
      scope: {
        afterLogin: '=',
        afterRegister: '=',
        afterReset: '=',
        selectedForm: '=?'
      },
      controllerAs: 'vm',
      bindToController: true,
      link: function(scope, element, attrs, ctrl) {},
      controller: function() {
        var validateLoginAsFailed, vm;
        vm = this;
        vm.creds = {
          email: '',
          password: ''
        };
        vm.user = {
          entity_attributes: {
            type: 'candidate'
          }
        };
        vm.selectEmployee = function() {
          return vm.user.entity_attributes.type = 'candidate';
        };
        vm.selectEmployer = function() {
          return vm.user.entity_attributes.type = 'employer';
        };
        vm.backToLoginForm = function() {
          return vm.selectedForm = 'default';
        };
        vm.selectedType = function(type) {
          return vm.user.entity_attributes.type === type;
        };
        vm.login = function() {
          return Auth.login(vm.creds).then(function(user) {
            return vm.afterLogin(user);
          }, function(err) {
            $log.error('Unable to login: ', err);
            return validateLoginAsFailed();
          });
        };
        vm.register = function() {
          angular.extend(vm.user, vm.creds);
          return Auth.register(vm.user).then(function(registeredUser) {
            vm.afterRegister(registeredUser);
            return console.log('CREATED!!!!', registeredUser);
          }, function(err) {
            return $log.error('Unable to register: ', err);
          });
        };
        validateLoginAsFailed = function() {
          vm.form.login.email.$setValidity('failed', false);
          return vm.form.login.password.$setValidity('failed', false);
        };
      }
    };
  });

}).call(this);




/* Session object containing user attributes */

(function() {
  this.TheCreem.service('CreemSession', function($q, $log, Auth) {
    var _self;
    _self = this;
    this.create = function(attributes) {
      this.user = attributes;
    };
    this.isAuthenticated = function() {
      return !!this.user;
    };
    this.destroy = function() {
      if (this.user) {
        Auth.reset();
      }
      this.user = null;
    };
    this.getCurrentUser = function() {
      return Auth.currentUser().then(function(user) {
        _self.create(user);
        return user;
      }, function(error) {
        $log.error('Could not authenticate current user: ', error);
        return error;
      });
    };
    return this;
  });

}).call(this);



(function() {
  this.TheCreem.controller('ProfileController', function() {
    var vm;
    vm = this;
    return this;
  });

}).call(this);



